import { ROUTER, ROUTER_MENU_NAME } from '@/router';
import { DownOutlined } from '@ant-design/icons';
import { Dropdown, Menu as MenuList } from 'antd';
import classNames from 'classnames';
import React, { useCallback, useState } from 'react';
import { isMobile } from 'react-device-detect';
import { Link, useHistory, useLocation } from 'react-router-dom';
import './index.scss';

const solutionSubMenuData = [
  { route: ROUTER.dataCenterService },
  { route: ROUTER.cloudHosting },
  { route: ROUTER.softwareService },
  { route: ROUTER.webDevelopment },
  {
    route: ROUTER.decentralizedStorageSolutions
  },
  { route: ROUTER.metaverse }
];

const aboutSubMenuData = [{ route: ROUTER.founder }, { route: ROUTER.career }];

const firstMenuData = [
  { route: ROUTER.home },
  {
    route: ROUTER.solution,
    overlayData: solutionSubMenuData
  },
  { route: ROUTER.ourPartners },
  {
    route: ROUTER.aboutUs,
    overlayData: aboutSubMenuData
  }
];

const mobileMenuData = [
  ROUTER.home,
  ROUTER.solution,
  ROUTER.dataCenterService,
  ROUTER.cloudHosting,
  ROUTER.softwareService,
  ROUTER.webDevelopment,
  ROUTER.decentralizedStorageSolutions,
  ROUTER.metaverse,
  ROUTER.ourPartners,
  ROUTER.aboutUs,
  ROUTER.founder,
  ROUTER.career,
  ROUTER.contactUs
];

const Menu: React.FC = () => {
  const history = useHistory();
  const { pathname } = useLocation();

  const [expanded, setExpanded] = useState(false);

  const getLink = useCallback(
    ({ route, overlayData = null, content = null }) => (
      <Link
        className={classNames('link-element', pathname === route && 'active')}
        to={route}
        onClick={() => {
          window.scroll(0, 0);
        }}
      >
        {content ? (
          content
        ) : overlayData ? (
          <Dropdown
            overlay={
              <MenuList
                items={overlayData.map((data) => ({
                  label: getLink({ route: data.route })
                }))}
              />
            }
          >
            <div>
              {ROUTER_MENU_NAME[route]} <DownOutlined />
            </div>
          </Dropdown>
        ) : (
          ROUTER_MENU_NAME[route]
        )}
      </Link>
    ),
    [pathname]
  );

  const toLink = useCallback(
    (link: string) => {
      history.push(link);
      window.scroll(0, 0);
      setExpanded(false);
    },
    [history]
  );

  return (
    <header className="menu">
      <div className="container">
        {getLink({ route: ROUTER.home, content: <div className="logo"></div> })}
        {isMobile ? (
          <div className="menu-trigger" onClick={() => setExpanded(true)}></div>
        ) : (
          <div className="menu-links">
            <ul style={{ margin: 0 }} className="links">
              {firstMenuData.map(({ route, overlayData }) => (
                <li key={ROUTER_MENU_NAME[route]} className="link-item">
                  {getLink({ route, overlayData })}
                </li>
              ))}
            </ul>
            {getLink({
              route: ROUTER.contactUs,
              content: (
                <button
                  style={{
                    width: '110px',
                    height: '32px',
                    borderRadius: '50px'
                  }}
                >
                  {ROUTER_MENU_NAME[ROUTER.contactUs]}
                </button>
              )
            })}
          </div>
        )}
        {isMobile && expanded && (
          <div className="menu-fillscreen">
            <div
              className="close-menu"
              onClick={() => setExpanded(false)}
            ></div>
            <ul className="links">
              {mobileMenuData.map((route) => (
                <li
                  key={ROUTER_MENU_NAME[route]}
                  className="link-item"
                  onClick={() => toLink(route)}
                >
                  <a
                    className={classNames(
                      'link-element',
                      pathname === route && 'active'
                    )}
                  >
                    {ROUTER_MENU_NAME[route]}
                  </a>
                </li>
              ))}
            </ul>
          </div>
        )}
      </div>
    </header>
  );
};

export default Menu;
