import React from 'react';
import { isMobile } from 'react-device-detect';
import './index.scss';

interface ImgProps {
  bgUrl: any;
  contain?: any;
  text1?: string;
  text2?: string;
}

const Imgbg: React.FC<ImgProps> = ({ bgUrl, text1, text2, contain }) => {
  return (
    <div
      className="imgBg"
      style={{
        height:
          !text1 && !text2 && isMobile
            ? '400px'
            : isMobile
            ? 'fit-content'
            : '520px',
        padding: (text1 || text2) && isMobile ? '60px 0 70px' : 0,
        background: `url(${bgUrl}) center / ${
          contain ? 'contain' : 'cover'
        } no-repeat`
      }}
    >
      <div className="bgContent">
        <div
          className="font-20"
          style={{ width: isMobile ? '100%' : 520, color: '#FFFFFF' }}
        >
          <div>{text1}</div>
          <div style={{ marginTop: 20 }}>{text2}</div>
        </div>
      </div>
    </div>
  );
};

export default Imgbg;
