import BackToTop from '@/components/back-to-top';
import Banner from '@/components/banner';
import Footer from '@/components/footer';
import Imgbg from '@/components/imgBanner';
import Loading from '@/components/loading';
import Menu from '@/components/menu';
import SubmitRequest from '@/components/submit-request';
import { ROUTER } from '@/router';
import React, {
  lazy,
  Suspense,
  useCallback,
  useEffect,
  useMemo,
  useState
} from 'react';
import { isMobile } from 'react-device-detect';
import { Redirect, Route, Switch } from 'react-router';
import { useLocation } from 'react-router-dom';

const Home = lazy(() => import('@/pages/home'));
const Solution = lazy(() => import('@/pages/solution'));
const Cloud = lazy(() => import('@/pages/cloud'));
const Web = lazy(() => import('@/pages/web'));
const Software = lazy(() => import('@/pages/software'));
const Data = lazy(() => import('@/pages/data'));
const About = lazy(() => import('@/pages/about'));
const Founder = lazy(() => import('@/pages/founder'));
const Career = lazy(() => import('@/pages/career'));
const Partner = lazy(() => import('@/pages/partner'));
const Decentralized = lazy(() => import('@/pages/decentralized'));
const Metaverse = lazy(() => import('@/pages/metaverse'));
const Contact = lazy(() => import('@/pages/contact'));
const Thank = lazy(() => import('@/pages/thank'));

const hasImgbgPageArr = [ROUTER.contactUs, ROUTER.thank, ROUTER.founder];
const hasSubmitRequestPageArr = [
  ROUTER.home,
  ROUTER.solution,
  ROUTER.aboutUs,
  ROUTER.contactUs,
  ROUTER.ourPartners,
  ROUTER.decentralizedStorageSolutions,
  ROUTER.founder,
  ROUTER.career,
  ROUTER.metaverse,
  ROUTER.thank
];

const DefaultLayout: React.FC = () => {
  const { pathname } = useLocation();
  const [message, setMessage] = useState('');

  const hasImgbgPage = useMemo(
    () =>
      hasImgbgPageArr.includes(pathname) ||
      (isMobile && pathname == ROUTER.aboutUs),
    [pathname]
  );

  const hasSubmitRequestPage = useMemo(
    () => hasSubmitRequestPageArr.includes(pathname),
    [pathname]
  );

  const goSubmit = useCallback((message = '') => {
    document.getElementById('bottom')?.scrollIntoView();
    setMessage(message);
  }, []);

  useEffect(() => {
    setMessage('');
  }, [pathname]);

  return (
    <div className="default-container">
      <Menu />
      <Banner />
      {!hasImgbgPage && <Imgbg />}
      <Suspense fallback={<Loading />}>
        <Switch>
          <Route path={ROUTER.home} exact>
            <Home />
          </Route>
          <Route path={ROUTER.solution} exact>
            <Solution />
          </Route>
          <Route path={ROUTER.cloudHosting} exact>
            <Cloud />
          </Route>
          <Route path={ROUTER.webDevelopment} exact>
            <Web />
          </Route>
          <Route path={ROUTER.softwareService} exact>
            <Software />
          </Route>
          <Route path={ROUTER.dataCenterService} exact>
            <Data />
          </Route>
          <Route path={ROUTER.aboutUs} exact>
            <About />
          </Route>
          <Route path={ROUTER.founder} exact>
            <Founder />
          </Route>
          <Route path={ROUTER.career} exact>
            <Career />
          </Route>
          <Route path={ROUTER.ourPartners} exact>
            <Partner />
          </Route>
          <Route path={ROUTER.decentralizedStorageSolutions} exact>
            <Decentralized />
          </Route>
          <Route path={ROUTER.metaverse} exact>
            <Metaverse />
          </Route>
          <Route path={ROUTER.contactUs} exact>
            <Contact />
          </Route>
          <Route path={ROUTER.thank} exact>
            <Thank />
          </Route>
          <Redirect from="*" to={ROUTER.home} />
        </Switch>
      </Suspense>
      <BackToTop />
      {!hasSubmitRequestPage && <SubmitRequest message={message} />}
      <Footer />
    </div>
  );
};

export default DefaultLayout;
