import LeftImg from '@/assets/imgs/imgs/left.svg';
import RightImg from '@/assets/imgs/imgs/right.svg';
import React from 'react';
import { isMobile } from 'react-device-detect';
import './index.scss';

interface TitleProps {
  title: any;
  titwidth?: string;
  lineHeight?: string;
  subtitle?: string;
  size?: string;
  subwidth?: string;
  subtop?: string;
  subsize?: string;
  subweight?: any;
  titTop?: string;
}

const TopTitle: React.FC<TitleProps> = ({
  title,
  titwidth,
  lineHeight,
  size,
  subtitle,
  subwidth,
  subtop,
  subsize,
  subweight,
  titTop
}) => {
  return (
    <div>
      <div className="top">
        <img className="left-img" src={LeftImg} />
        <div
          className="centertext"
          style={{
            fontSize: isMobile ? size || '42px' : size || '64px',
            width: titwidth,
            lineHeight
          }}
        >
          {title}
        </div>
        <img className="right-img" src={RightImg} />
      </div>
      <div
        className="toptext"
        style={{
          width: subwidth,
          position: 'relative',
          top: subtop,
          fontSize: subsize,
          fontWeight: subweight
        }}
      >
        {subtitle}
      </div>
    </div>
  );
};

export default TopTitle;
